'use '
import React from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';

export default function BannerSlider() {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
    return (
        <div className='w-full'>
            <Slider {...settings}>
                <div>
                    <img className='w-full h-[80vh]' src="https://www.inframedesigninstitute.com/images/homesliderpic/1732701125MOCK_TEST01.jpg" alt="" />
                </div>
                <div>
                    <img className='w-full h-[80vh]' src="https://www.inframedesigninstitute.com/images/homesliderpic/1732701125MOCK_TEST01.jpg" alt="" />
                </div>
                <div>
                    <img className='w-full h-[80vh]' src="https://www.inframedesigninstitute.com/images/homesliderpic/1732701125MOCK_TEST01.jpg" alt="" />
                </div>
            </Slider>
        </div>
    )
}
